.taroify-toast {
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: var(--toast-width, 176px);
  max-width: var(--toast-max-width, 70%);
  min-height: var(--toast-min-height, 176px);
  padding: var(--toast-padding, var(--padding-md, 32px));
  font-size: var(--toast-font-size, var(--font-size-md, 28px));
  line-height: var(--toast-line-height, var(--line-height-md, 40px));
  color: var(--toast-color, var(--white, #fff));
  text-align: center;
  word-break: break-all;
  white-space: pre-wrap;
  background: var(--toast-background-color, rgba(var(--black-rgb, 0, 0, 0), 0.7));
  border-radius: var(--toast-border-radius, var(--border-radius-lg, 16px));
  transition: all var(--toast-transition-duration, var(--animation-duration-fast, 0.2s));
}
.taroify-toast--text, .taroify-toast--html {
  width: fit-content;
  min-width: var(--toast-text-min-width, 192px);
  min-height: 0;
  padding: var(--toast-text-padding, var(--padding-xs, 16px) var(--padding-sm, 24px));
}
.taroify-toast--text .taroify-toast__message, .taroify-toast--html .taroify-toast__message {
  margin-top: 0;
}
.taroify-toast--top {
  top: var(--toast-position-top-distance, var(--toast-position-distance, 20%));
}
.taroify-toast--bottom {
  top: auto;
  bottom: var(--toast-position-bottom-distance, var(--toast-position-distance, 20%));
}
.taroify-toast__icon {
  font-size: var(--toast-icon-font-size, 72px);
  color: var(--toast-icon-color, var(--white, #fff));
}
.taroify-toast__loading {
  padding: var(--toast-loading-padding, 6px);
  color: var(--toast-loading-color, var(--white, #fff));
}
.taroify-toast__message {
  margin-top: var(--padding-xs, 16px);
}