.taroify-badge--content {
  box-sizing: border-box;
  display: inline-block;
  min-width: var(--badge-size, 32px);
  padding: var(--badge-padding, 0 6px);
  font-family: var(--badge-font-family, -apple-system-font, helvetica neue, arial, sans-serif);
  font-size: var(--badge-font-size, var(--font-size-sm, 24px));
  font-weight: var(--badge-font-weight, var(--font-weight-bold, 500));
  line-height: 1.2;
  color: var(--badge-color, var(--white, #fff));
  text-align: center;
  background: var(--badge-background-color, var(--red, #ee0a24));
  border: var(--badge-border-width, var(--border-width-base, 2px)) solid var(--badge-border-color, var(--white, #fff));
  border-radius: var(--badge-border-radius, var(--border-radius-max, 999px));
}
.taroify-badge--dot {
  width: var(--badge-dot-size, 8PX);
  min-width: 0;
  height: var(--badge-dot-size, 8PX);
  padding: 0;
  background: var(--badge-dot-color, var(--badge-background-color, var(--red, #ee0a24)));
  border-radius: 100%;
}
.taroify-badge--fixed {
  position: absolute;
  transform-origin: 100%;
}
.taroify-badge--top-left {
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}
.taroify-badge--top-right {
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
}
.taroify-badge--bottom-left {
  bottom: 0;
  left: 0;
  transform: translate(-50%, 50%);
}
.taroify-badge--bottom-right {
  right: 0;
  bottom: 0;
  transform: translate(50%, 50%);
}

.taroify-badge-wrapper {
  position: relative;
  display: inline-block;
}