.taroify-grid {
  display: flex;
  flex-wrap: wrap;
}

.taroify-grid-item {
  position: relative;
  box-sizing: border-box;
}
.taroify-grid-item--square {
  height: 0;
}
.taroify-grid-item__icon {
  font-size: var(--grid-item-icon-font-size, 56px);
}
.taroify-grid-item__icon .taroify-icon {
  font-size: inherit;
}
.taroify-grid-item__text {
  font-size: var(--grid-item-text-font-size, var(--font-size-sm, 24px));
  line-height: 1.5;
  color: var(--grid-item-text-color, var(--gray-7, #646566));
  word-break: break-all;
}
.taroify-grid-item__icon + .taroify-grid-item__text {
  margin-top: var(--padding-xs, 16px);
}
.taroify-grid-item__content {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: var(--grid-item-content-padding, var(--padding-md, 32px) var(--padding-xs, 16px));
  background-color: var(--grid-item-content-background-color, var(--white, #fff));
}
.taroify-grid-item__content::after {
  z-index: 1;
  border-width: 0 var(--border-width-base, 2px) var(--border-width-base, 2px) 0;
}
.taroify-grid-item__content--square {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.taroify-grid-item__content--centered {
  align-items: center;
  justify-content: center;
}
.taroify-grid-item__content--surround::after {
  border-width: var(--border-width-base, 2px);
}
.taroify-grid-item__content--horizontal {
  flex-direction: row;
}
.taroify-grid-item__content--horizontal .taroify-grid-item__icon + .taroify-grid-item__text {
  margin-top: 0;
  margin-left: var(--padding-xs, 16px);
}
.taroify-grid-item__content--clickable {
  cursor: pointer;
}
.taroify-grid-item__content--clickable:active {
  background-color: var(--grid-item-content-active-color, var(--active-color, var(--gray-2, #f2f3f5)));
}