[class*=taroify-] {
  -webkit-tap-highlight-color: transparent;
}

a {
  text-decoration: none;
}

input,
button,
textarea {
  font-size: inherit;
  color: inherit;
}

a:focus,
input:focus,
button:focus,
textarea:focus,
[class*=taroify-]:focus {
  outline: none;
}

ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@keyframes taroify--rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.taroify-clearfix::after {
  display: table;
  clear: both;
  content: "";
}

.taroify-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.taroify-ellipsis--l2 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre-wrap;
  /* stylelint-disable */
  /* autoprefixer: ignore next */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -webkit-line-clamp: 2;
  /* stylelint-enable */
}

.taroify-ellipsis--l3 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre-wrap;
  /* stylelint-disable */
  /* autoprefixer: ignore next */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -webkit-line-clamp: 3;
  /* stylelint-enable */
}

[class*=taroify-hairline]::after {
  position: absolute;
  box-sizing: border-box;
  pointer-events: none;
  content: " ";
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid var(--gray-3, #ebedf0);
  transform: scale(0.5);
}

.taroify-hairline, .taroify-hairline--top, .taroify-hairline--left, .taroify-hairline--right, .taroify-hairline--bottom, .taroify-hairline--surround, .taroify-hairline--top-bottom {
  position: relative;
}
.taroify-hairline--top::after {
  border-top-width: 2px;
}
.taroify-hairline--left::after {
  border-left-width: 2px;
}
.taroify-hairline--right::after {
  border-right-width: 2px;
}
.taroify-hairline--bottom::after {
  border-bottom-width: 2px;
}
.taroify-hairline--top-bottom::after, .taroify-hairline-unset--top-bottom::after {
  border-width: 2px 0;
}
.taroify-hairline--surround::after {
  border-width: 2px;
}